<template>
  <div class="pay">
    <van-nav-bar title="" left-text="" left-arrow @click-left="handleLeftClose" />
    <van-row>
      <van-col span="24">
        <van-steps :active="step">
          <van-step></van-step>
          <van-step></van-step>
          <van-step></van-step>
        </van-steps>
      </van-col>
    </van-row>
    <van-row :gutter="20" style="margin-top: 20px;">
      <van-col :span="24" class="bill">
        
        <div>299.00</div>
      </van-col>
      <van-col :span="24" class="set">
        <div class="set-title"></div>
        <el-table :data="tableData" style="width: 100%" border>
          <el-table-column prop="name" label="" width="150">
          </el-table-column>
          <el-table-column prop="money" label="" width="150">
          </el-table-column>
          <el-table-column label="">
            <el-table-column prop="data" label="" width="120">
            </el-table-column>
            <el-table-column prop="voice" label="" width="120">
            </el-table-column>
            <el-table-column prop="range" label="" width="120">
            </el-table-column>
          </el-table-column>
          <el-table-column label="">
            <el-table-column prop="fee" label="/" width="120">
            </el-table-column>
            <el-table-column prop="plan" label="" width="120">
            </el-table-column>
            <el-table-column prop="text" label="" width="120">
            </el-table-column>
          </el-table-column>
          <el-table-column prop="gift" label="" width="150">
          </el-table-column>
        </el-table>
      </van-col>

      <van-col span="24" class="info strong">
        
      </van-col>
      <van-col span="24" class="info" v-for="(item,index) in setInfo" :key="index">{{item}}</van-col>
      <van-col :span="24">
        <van-button type="primary" bottom-action class="btn" @click="handlePay"></van-button>
      </van-col>
    </van-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      wxparams: {},
      step: 2,
      checked: '',
      tableData: [
        {
          name: '',
          money: '69',
          data: '',
          voice: '300',
          range: '',
          fee: 0.15,
          plan: '20GB1Mbps',
          text: '0.1/',
          gift: ''
        }
      ],
      setInfo: [
        '1.20GB1Mbps',
        '2.4G3G2GWLANWi-Fi',
        '3.',
        '4.',
        '5.',
        '6.'
      ]
    }
  },
  methods: {
    handleLeftClose() {
      this.$dialog
        .confirm({
          title: '',
          message: ''
        })
        .then(() => {
          this.$router.push({ name: 'OpenAccount' })
        })
        .catch(() => {
          // on cancel
        })
    },
    handlePay() {
      this.$dialog
        .confirm({
          title: '',
          message: '299'
        })
        .then(() => {
          this.payAction()
        })
        .catch(() => {
          this.$toast.fail('')
        })
    },
    runWeixinJS(cb) {
      if (typeof window.WeixinJSBridge === 'undefined') {
        if (document.addEventListener) {
          document.addEventListener('WeixinJSBridgeReady', cb, false)
        } else if (document.attachEvent) {
          document.attachEvent('WeixinJSBridgeReady', cb)
          document.attachEvent('onWeixinJSBridgeReady', cb)
        }
      } else {
        cb()
      }
    },
    wechatpay() {
      const vm = this
      const data = this.wxparams
      window.WeixinJSBridge.invoke(
        'getBrandWCPayRequest',
        {
          appId: data.appId,
          timeStamp: data.timeStamp,
          nonceStr: data.nonceStr,
          package: data.package,
          signType: data.signType,
          paySign: data.paySign
        },
        function(res) {
          if (res.err_msg === 'get_brand_wcpay_request:ok') {
            console.log('get_brand_wcpay_request:ok')
            vm.$toast.success('')
            vm.$router.push({ name: 'PaySuccess' })
          } else if (res.err_msg === 'get_brand_wcpay_request:cancel') {
            WeixinJSBridge.invoke('closeWindow', {}, function(res) {
              console.log('get_brand_wcpay_request:cancel')
              vm.$toast.success('get_brand_wcpay_request:cancel')
            })
          } else {
            WeixinJSBridge.invoke('closeWindow', {}, function(res) {
              console.log('get_brand_wcpay_request:fail')
              vm.$toast.success('get_brand_wcpay_request:fail')
            })
          }
        }
      )
    },
    payAction() {
      const vm = this

      const params = {
        money: 299,
        wxId:
          this.$store.state.userinfo.openid || 'oDtJ10RDKfRcoGTUVFySkeztR7Ko',
        openid:
          this.$store.state.userinfo.openid || 'oDtJ10RDKfRcoGTUVFySkeztR7Ko',
        orderId: this.$route.query.orderId || '1',
        cardDetailId: this.$route.query.cardDetailId || '1'
      }

      this.$api.PayAction(params).then(res => {
        if (res.data.code == '200') {
          this.wxparams = res.data.data
          vm.runWeixinJS(vm.wechatpay)
        }
      })
    }
  }
}
</script>


<style lang="scss" scoped>
.pay {
  text-align: center;
}

.bill {
  color: #cc0000;
  font-size: 20px;
  div {
    color: #cc0000;
  }
}

.info {
  text-align: left;
  font-size: 12px;
  margin: 5px 0;
  &.strong {
    font-weight: bold;
    font-size: 16px;
    margin: 10px 0;
    color: #fb8522;
  }
}
</style>


<style lang="scss" >
.pay {
  .el-table th,
  .el-table tr {
    font-size: 11px;
  }

  .el-table th {
    background: #ffebd8;
    color: #fb8522;
    padding: 5px;
  }
  .el-table td {
    padding: 0;
  }
  .el-table--border {
  }

  .set-title {
    text-align: left;
    color: #fb8522;
    border-left: 6px solid #fb8522;
    padding: 10px 0;
    text-indent: 0.5em;
    width: 160px;
    margin: 10px 0 4px 0;
  }
}
</style>




