<template>
  <div>
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" />
    <van-row :gutter="20" style="margin-top: 20px;">
      <van-col :span="24">
        <van-radio-group v-model="radio">
          <van-radio name="1"></van-radio>
          <van-radio name="2"></van-radio>
          <van-radio name="3"></van-radio>
        </van-radio-group>
      </van-col>

      <van-col :span="24" style="margin:20px 0;">
        <van-cell-group>
          <van-field v-model="value" placeholder="" label="" v-if="radio === '1'" />
          <van-field v-model="value" placeholder="" label="" v-else-if="radio === '2'" />
          <van-field v-model="value" placeholder="" label="" v-else />
        </van-cell-group>
      </van-col>

      <van-button type="primary" bottom-action> </van-button>
    </van-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      radio: '1',
      value: ''
    }
  },
  watch: {
    radio(val, oldVal) {
      this.value = ''
    }
  }
}
</script>




