<template>
  <div class="answer">
    <van-popup v-model="overlay" :close-on-click-overlay="false" class="lay-base">
      <div class="lay-cnt">
        <div class="img"></div>
        <div class="cnt">
          <span class="fs20">{{ list.length + list1.length + list2.length }}</span> </div>
        <div class="cnt-2 fs20"></div>
        <div class="lay-box-hold">
          <div class="lay-box lay-box1">
            <div class="lay-box-top"></div>
            <div class="lay-box-btm">
              <span class="fs20">{{list.length}}</span> </div>
          </div>

          <div class="lay-box lay-box2">
            <div class="lay-box-top"></div>
            <div class="lay-box-btm">
              <span class="fs20">{{list1.length}}</span></div>
          </div>

          <div class="lay-box lay-box3">
            <div class="lay-box-top"></div>
            <div class="lay-box-btm">
              <span class="fs20">{{list2.length}}</span></div>
          </div>
        </div>
        <div class="lay-btn">
          <van-button type="danger" @click="$router.push({name: 'OpenAccount'})"></van-button>
        </div>
      </div>

    </van-popup>
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" />

    <wap-subtitle subtitle="" style="margin: 20px 0 0 0;"></wap-subtitle>
    <van-list>
      <van-cell v-for="item in list" :key="item.name" :title="item.name" :value="'' + item.percent + '%'" is-link :to="{name:'ReportItem',params: {id: item.id}}" />
    </van-list>

    <wap-subtitle subtitle="" style="margin: 20px 0 0 0;"></wap-subtitle>
    <van-list>
      <van-cell v-for="item in list1" :key="item.name" :title="item.name" :value="'' + item.percent + '%'" is-link :to="{name:'ReportItem',params: {id: item.id}}" />
    </van-list>

    <wap-subtitle subtitle="" style="margin: 20px 0 0 0;"></wap-subtitle>
    <van-list>
      <van-cell v-for="item in list2" :key="item.name" :title="item.name" :value="'' + item.percent + '%'" is-link :to="{name:'ReportItem',params: {id: item.id}}" />
    </van-list>

    <van-row :gutter="20">
      <van-col span="24" class="tip-red" style="font-size: 14px;"></van-col>
      <van-col span="24" class="tip-red" v-for="(item,index) in tipInfo" :key="index">{{item}}</van-col>
    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      overlay: true,
      tipInfo: [
        '1.',
        '2.4;',
        '3.',
        '4.'
      ],
      list: [
        {
          name: '',
          percent: '60',
          id: '11'
        },
        {
          name: '',
          percent: '55',
          id: '12'
        }
      ],
      list1: [
        {
          name: '',
          percent: '90',
          id: '21'
        },
        {
          name: '',
          percent: '85',
          id: '22'
        }
      ],
      list2: [
        {
          name: '',
          percent: '91',
          id: '31'
        },
        {
          name: '',
          percent: '90',
          id: '32'
        },
        {
          name: '',
          percent: '87',
          id: '33'
        }
      ]
    }
  },

  methods: {}
}
</script>


<style lang="scss" >
.lay-base {
  width: 80%;
  border-radius: 10px;
  .lay-cnt {
    background: #fff;
    color: #fff;
    height: 300px;
    width: 100%;
    border-radius: 10px;

    position: relative;
    .img {
      width: 100%;
      height: 220px;
      background-image: url(/static/imgs/report-bg.jpg);
      background-repeat: no-repeat;
      border-radius: 10px 10px 0 0;
    }
    .cnt {
      position: absolute;
      top: 20px;
      left: 20px;
    }

    .cnt-2 {
      position: absolute;
      top: 60px;
      left: 20px;
    }
    .fs20 {
      font-size: 20px;
    }

    .lay-box-hold {
      position: absolute;
      top: 120px;
      left: 20px;
      width: 86%;
      display: flex;
      justify-content: space-between;

      .lay-box {
        width: 60px;
        text-align: center;
        border-radius: 10px;
        .lay-box-top {
          color: #fff;
          height: 30px;
          line-height: 30px;
          width: 100%;
        }
        .lay-box-btm {
          height: 30px;
          line-height: 30px;
          background: #fff;
          color: #333333;
        }
      }

      .lay-box1 {
        .lay-box-top {
          background-color: #ff0033;
        }

        .lay-box-btm {
          span {
            color: #ff0033;
          }
        }
      }

      .lay-box2 {
        .lay-box-top {
          background-color: #ff9933;
        }

        .lay-box-btm {
          span {
            color: #ff9933;
          }
        }
      }

      .lay-box3 {
        .lay-box-top {
          background-color: #33cc66;
        }

        .lay-box-btm {
          span {
            color: #33cc66;
          }
        }
      }
    }

    .lay-btn {
      text-align: center;
      height: 80px;
      line-height: 80px;
    }
  }
}
</style>



