<template>
  <div class="apply">
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" />
    <van-row gutter="20">
      <van-col span="24" class="subtitle">
        <div class="subname"> </div>
        <div class="cnt">
          2000
        </div>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-radio-group v-model="form.category" class="radio">
            <van-radio name="1" style="padding:0 10px;"></van-radio>
            <van-radio name="2" style="padding:0 10px;"></van-radio>
          </van-radio-group>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.score" placeholder="" label="" type="number" required/>
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.local" placeholder="" label="" disabled required/>
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.provinceId" placeholder="" label="" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.level" placeholder="" label="" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.major" placeholder="" label="" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <div class="times-tip">{{times}}</div>
      </van-col>
      <van-col span="24">
        <van-button type="primary" bottom-action class="btn" @click="handleConfirm"></van-button>
      </van-col>

    </van-row>
    <van-row :gutter="20">
      <van-col span="24" class="tip-red">
        
      </van-col>
    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      form: {
        local: '',
        localId: '220000',
        category: '1'
      },
      times: '10'
    }
  },
  methods: {
    handleLocalClick() {},
    handleConfirm() {
      this.$dialog
        .confirm({
          title: '',
          message: ''
        })
        .then(() => {
          this.$router.push({ name: 'ReportList' })
        })
        .catch(() => {
          this.$toast.fail('')
        })
    }
  },
  created() {}
}
</script>

<style lang="scss" scoped>
.apply {
  height: 100vh;
  .btn {
    margin-top: 20px;
  }
}

.radio {
  display: flex;
  padding: 10px;
  text-align: center;
  justify-content: space-evenly;
}

.subtitle {
  font-size: 20px;
  color: #cc0000;
  padding-top: 10px;
  padding-bottom: 10px;
  .subname {
    text-align: center;
    padding: 15px;
    &:nth-child(2) {
      color: #000;
      padding: 5px 0;
    }
  }
  .cnt {
    font-size: 13px;
    color: #333333;
    span {
      color: #cc0000;
    }
  }
}

.tip-red {
  color: #cc0000;
  font-size: 12px;
  padding-top: 10px;
}
</style>


