<template>
  <div>
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.push({name: 'Home'})" @click-right="handlePost" />
    <van-row gutter="20">

      <van-col span="24">
        <div>
          <img src="/static/imgs/kaika.png" alt="">
        </div>
        <div class="ad">
           <br> 1

        </div>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field value="299.00" label="" disabled required/>
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field value="" label="" disabled required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group style="position:relative;">
          <van-field value="" label="" disabled required/>
          <van-radio-group v-model="form.set" class="radio">
            <!-- <van-radio name="1" style="padding:0 10px;">49</van-radio> -->
            <van-radio name="69" style="padding:0 10px;">69</van-radio>
          </van-radio-group>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-cell title="" :value="form.cityText" @click="showCityPicker = true" class="sg-form" required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-cell title="" :value="form.number" class="sg-form" required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-radio-group v-model="form.numberId">
          <van-row :gutter="20">
            <van-col :span="12" v-for="(item) in list" :key="item.id" class="phonenumber">
              <van-radio :name="item.id">{{item.phoneNo}}</van-radio>
            </van-col>
            <section class="sg-tips">
              <van-col span="24" class="subitle">
                
              </van-col>
              <van-col span="24">
                9969
              </van-col>
              <van-col span="24">
                VIP305

              </van-col>
              <van-col span="24">
                69
              </van-col>
              <van-col span="24">
                
              </van-col>
              <van-col span="24">
                300
              </van-col>
              <van-col span="24">
                
              </van-col>
              <van-col span="24">
                /0.15
              </van-col>
              <van-col span="24">
                20GB1Mbps
              </van-col>
              <van-col span="24">
                /0.1
              </van-col>
              <van-col span="24" class="subitle">
                
              </van-col>
              <van-col span="24">
                1.20GB1Mbps

              </van-col>
              <van-col span="24">
                2.4G3G2GWLANWi-Fi
              </van-col>
              <van-col span="24">
                3.
              </van-col>
              <van-col span="24">
                4.
              </van-col>
              <van-col span="24">
                5.
              </van-col>
              <van-col span="24">
                6.
              </van-col>
            </section>

          </van-row>
        </van-radio-group>
      </van-col>
      <van-col span="24" class="btm-picker">
        <van-picker show-toolbar title="" :columns="columns" @cancel="onCancel" @confirm="onConfirm" v-show="showCityPicker" />
      </van-col>
    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      form: {
        number: '',
        set: '69',
        cityId: '',
        numberId: ''
      },
      list: [],
      columns: this.$cityList,
      showCityPicker: false
    }
  },

  watch: {
    'form.numberId': {
      immediate: true,
      handler(val) {
        if (val && val !== '') {
          this.form.number = this.$_.find(this.list, { id: val }).phoneNo
        }
      }
    },
    'form.cityId': {
      immediate: true,
      handler(val) {
        if (val && val !== '') {
          this.FetchData({ cityId: val })
        }
      }
    }
  },
  methods: {
    handleSelectNo() {},
    onConfirm(value, index) {
      this.form.cityId = value.id
      this.form.cityText = value.text
      this.showCityPicker = false
    },
    onCancel() {
      this.showCityPicker = false
    },
    FetchData(params) {
      const vm = this
      vm.$store.commit(vm.$types.ShowLoading, true)
      vm.$api
        .GetPhoneNumber(params)
        .then(res => {
          if (res.data.code == '200') {
            vm.list = res.data.data
          } else {
            vm.$toast.fail(``)
          }
          vm.$store.commit(vm.$types.ShowLoading, false)
        })
        .catch(err => {
          vm.$toast.fail(JSON.stringify(err))
          vm.$store.commit(vm.$types.ShowLoading, false)
        })
    },
    handlePost() {
      const params = {
        money: 299,
        cardType: this.form.set,
        phoneNo: this.form.number,
        cityId: this.form.cityId,
        numberId: this.form.numberId
      }

      for (let [key, value] of Object.entries(params)) {
        if (!value && key === 'numberid') {
          this.$toast.fail(``)
          return false
        }
        if (!value) {
          this.$toast.fail(``)
          return false
        }
      }

      params.wxId = this.$store.state.userinfo.openid
      const vm = this
      vm.$store.commit(vm.$types.ShowLoading, true)

      vm.$api
        .SaveBaseInfo(params)
        .then(res => {
          if (res.data.code == '200') {
            const query = Object.assign(
              { cardId: vm.form.numberId },
              res.data.data
            )
            console.log(query)
            vm.$store.commit(vm.$types.ShowLoading, false)
            vm.$router.push({ name: 'InputInfo', query: query })
          } else {
            vm.$store.commit(vm.$types.ShowLoading, false)
            vm.$toast.fail(res.data.message)
          }
        })
        .catch(err => {
          vm.$toast.fail(JSON.stringify(err))
          vm.$store.commit(vm.$types.ShowLoading, false)
        })
    }
  },
  created() {}
}
</script>


<style lang="scss" scoped>
.radio {
  display: flex;
  padding: 10px;
  position: absolute;
  z-index: 1000;
  left: 80px;
  top: 0;
}

.phonenumber {
  padding: 5px 0;
  text-align: center;
  font-size: 13px;
}

.ad {
  font-size: 16px;
  color: #cc0000;
  text-align: center;
  padding: 10px;
  margin: 0 20px;
}

img {
  width: 100%;
}
.sg-tips {
  font-size: 12px;
  .subitle {
    font-size: 14px;
    padding: 5px;
    color: #cc0000;
  }
}
</style>



