<template>
  <div>
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="handleLeftClose" @click-right="handlePost" />
    <!-- <van-nav-bar title="" right-text="" @click-right="handlePost" /> -->

    <van-row>
      <van-col span="24">
        <van-steps :active="step">
          <van-step></van-step>
          <van-step></van-step>
          <van-step></van-step>
        </van-steps>
      </van-col>
    </van-row>

    <van-row>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field value="" placeholder="" label="" disabled required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-cell title="" :value="form.cityText" @click="showCityPicker = true" class="sg-form" required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.detail" placeholder="" label="" required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.postal" placeholder="6" label="" required maxlength="6" :error="postalError" :error-message="postalErrorMsg" />
        </van-cell-group>
      </van-col>
      <van-col span="24" style="position:relative;">
        <van-cell-group>
          <van-field v-model="form.number" placeholder="11" label="" required maxlength="11" :error="numberError" :error-message="numberErrorMsg" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field value="" placeholder="" label="" disabled required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group style="position:relative;">
          <van-radio-group v-model="form.shipSet" class="radio">
            <van-cell required>
              <van-radio name="1"></van-radio>
              <van-radio name="2"></van-radio>
              <van-radio name="3"></van-radio>
            </van-cell>
          </van-radio-group>
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <span class="phone-tip"></span>
      </van-col>

      <van-col span="24" class="btm-picker">
        <van-picker show-toolbar title="" :columns="columns" @cancel="onCancel" @confirm="onConfirm" v-show="showCityPicker" />
      </van-col>
    </van-row>
  </div>
</template>


<script>
const _numberErrorMsg = ''
const _postalErrorMsg = ''

export default {
  data() {
    return {
      step: 1,
      form: {
        shipSet: '1',
        name: '',
        detail: '',
        postal: '',
        number: '',
        cityId: ''
      },
      columns: this.$cityWholeList,
      showCityPicker: false,
      numberError: false,
      numberErrorMsg: '',
      postalError: false,
      postalErrorMsg: ''
    }
  },
  watch: {
    'form.number': {
      handler(val, oldVal) {
        this.numberError = false
        this.numberErrorMsg = ''
      }
    },
    'form.postal': {
      handler(val, oldVal) {
        this.postalError = false
        this.postalErrorMsg = ''
      }
    }
  },
  methods: {
    handleLeftClose() {
      this.$dialog
        .confirm({
          title: '',
          message: ''
        })
        .then(() => {
          this.$router.push({ name: 'OpenAccount' })
        })
        .catch(() => {
          // on cancel
        })
    },
    validate() {
      let flag = true
      if (!RegExp(/^(1[0-9])[0-9]{9}$/).test(this.form.number)) {
        this.$toast.fail(_numberErrorMsg)
        this.numberErrorMsg = _numberErrorMsg
        this.numberError = true
        flag = false
      }

      if (!RegExp(/^[1-9]\d{5}(?!\d)$/).test(this.form.postal)) {
        this.$toast.fail(_postalErrorMsg)
        this.postalErrorMsg = _postalErrorMsg
        this.postalError = true
        flag = false
      }

      return flag
    },
    onConfirm(value, index) {
      this.form.cityId = value.id
      this.form.cityText = value.text
      this.showCityPicker = false
    },
    onCancel() {
      this.showCityPicker = false
    },
    handlePost() {
      if (!this.validate()) {
        return false
      }

      const params = {
        receivePerson: this.form.name,
        cityCode: this.form.cityId,
        cityName: this.form.cityText,
        receiveAddress: this.form.detail,
        postalNo: this.form.postal,
        ownerPhone: this.form.number,
        shipSet: this.form.shipSet
      }
      console.log(params)
      for (let [key, value] of Object.entries(params)) {
        if (!value) {
          this.$toast.fail(``)
          return false
        }
      }

      params.shipMethod = 0
      params.wxId = this.$store.state.userinfo.openid
      params.provinceName = ''
      params.provinceCode = '220000'

      params.orderId = this.$route.query.orderId || '1'
      params.cardDetailId = this.$route.query.cardDetailId || '1'
      // this.$router.push({ name: 'PayAccount' })

      this.$api.SaveShipInfo(params).then(res => {
        if (res.data.code == 200) {
          this.$router.push({ name: 'PayAccount', query: this.$route.query })
        } else {
          this.$toast.fail(res.data.message)
        }
      })
    }
  }
}
</script>


<style lang="scss" scoped>
.tips {
  color: #cc0000;
  font-size: 11px;
  padding: 10px;
}

.phone-tip {
  color: #cc0000;
  padding-left: 10px;
  // position: absolute;
  // top: 30px;
  font-size: 12px;
}
</style>



