<template>
  <div>
    <van-nav-bar title="" left-text="" left-arrow @click-left="$router.back()" />

    <van-row>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.id" placeholder="" label="" required/>
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.phoneNo" label="" required placeholder="11" maxlength="11" :error="numberError" :error-message="numberErrorMsg" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <!-- <van-field v-model="form.shipNo" placeholder="" label="" maxlength="23" disabled/> -->
          <div class="ship-id"></div>
          <div class="ship">
            <el-input size="mini" class="ship-input" :maxlength="4" type="tel" v-model="ship.no1" ref="ship1"></el-input>
            <el-input size="mini" class="ship-input" :maxlength="4" type="tel" v-model="ship.no2" ref="ship2"></el-input>
            <el-input size="mini" class="ship-input" :maxlength="4" type="tel" v-model="ship.no3" ref="ship3"></el-input>
            <el-input size="mini" class="ship-input" :maxlength="4" type="tel" v-model="ship.no4" ref="ship4"></el-input>
            <el-input size="mini" class="ship-input" :maxlength="3" type="tel" v-model="ship.no5" ref="ship5"></el-input>
          </div>
        </van-cell-group>
      </van-col>

      <van-col span="24" class="card-tip" @click.native="showDialog = true"></van-col>

      <van-col span="24" class="active-name">
        
      </van-col>
      <van-col span="24" class="active-cnt">
        1. VIP
      </van-col>
      <van-col span="24" class="active-cnt">
        2. 
      </van-col>
      <van-col span="24" class="active-cnt">
        3. 1-5
      </van-col>
      <van-col span="24" class="active-tips">
        10000
      </van-col>
      <van-col span="24" style="margin-top:20px;">
        <van-button type="primary" bottom-action class="btn" @click="handleSubmit" :disabled="btnDisabled"></van-button>
      </van-col>
    </van-row>

    <van-dialog v-model="showDialog" confirm-button-text="">
      <img src="/static/imgs/card-tip.png" alt="" style="width: 100%;">
    </van-dialog>
  </div>
</template>


<script>
const _numberErrorMsg = ''

export default {
  data() {
    return {
      form: {
        id: '',
        name: '',
        phoneNo: '',
        shipNo: ''
      },
      ship: {
        no1: '',
        no2: '',
        no3: '',
        no4: '',
        no5: ''
      },
      showDialog: false,
      btnDisabled: true,
      numberError: false,
      numberErrorMsg: ''
    }
  },
  watch: {
    form: {
      deep: true,
      immediate: true,
      handler(val) {
        if (val.id === '' || val.name === '' || val.phoneNo === '') {
          this.btnDisabled = true
        } else {
          this.btnDisabled = false
        }
      }
    },
    ship: {
      deep: true,
      immediate: true,
      handler(val) {
        const vm = this
        for (let [key, value] of Object.entries(val)) {
          if (value.toString().length === 4) {
            const index = ~~key[2] + 1
            vm.$refs[`ship${index}`].focus()
          }
        }
      }
    },
    'form.phoneNo': {
      handler(val, oldVal) {
        this.numberError = false
        this.numberErrorMsg = ''
      }
    }
  },
  methods: {
    validate() {
      let flag = true
      if (!RegExp(/^(1[0-9])[0-9]{9}$/).test(this.form.phoneNo)) {
        this.$toast.fail(_numberErrorMsg)
        this.numberErrorMsg = _numberErrorMsg
        this.numberError = true
        flag = false
      }

      return flag
    },
    handleSubmit() {
      const vm = this

      if (!this.validate()) {
        return false
      }

      vm.$store.commit(vm.$types.ShowLoading, true)
      const params = {
        name: this.form.name,
        idCard: this.form.id,
        phoneNo: this.form.phoneNo,
        uimNo:
          this.ship.no1 +
          this.ship.no2 +
          this.ship.no3 +
          this.ship.no4 +
          this.ship.no5
      }
      vm.$api
        .ActiveCard(params)
        .then(res => {
          if (res.data.code == '200') {
            // ''
            vm.$dialog
              .alert({
                message:
                  '305'
              })
              .then(() => {
                vm.$router.push({ name: 'Home' })
              })
          } else if (res.data.code == '500') {
            // '305'
            vm.$dialog
              .alert({
                message: res.data.message
              })
              .then(() => {
                // vm.$router.push({ name: 'Home' })
              })
          } else {
            vm.$toast.fail(res.data.message)
          }
          vm.$store.commit(vm.$types.ShowLoading, false)
        })
        .catch(err => {
          vm.$toast.fail(JSON.stringify(err))
          vm.$store.commit(vm.$types.ShowLoading, false)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.active-name,
.active-cnt,
.active-tips {
  color: #cc0000;
  font-size: 12px;
}

.active-name {
  padding: 10px;
}

.active-cnt,
.active-tips {
  padding: 0 10px;
}

.card-tip {
  text-align: center;
  font-size: 12px;
  padding: 10px;
  color: #0066ff;
}
.ship {
  text-align: center;
  margin: 10px 0;
  padding-bottom: 20px;
  .ship-input {
    width: 65px;
    font-size: 16px;
  }
}

.ship-id {
  color: #333;
  font-size: 14px;
  width: 100%;
  text-align: center;
  padding: 10px 0 0 0;
}
</style>
<style lang="scss">
.el-input__inner {
  padding: 0 !important;
  text-align: center;
  font-size: 11px;
}
</style>






