    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" />

    <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>

    <van-row :gutter="20" class="info">
      <van-col span="24" class="info-item"> 00101</van-col>
      <van-col span="24" class="info-item"> </van-col>
      <van-col span="24" class="info-item">100001</van-col>
      <van-col span="24" class="info-item"> 590</van-col>
      <van-col span="24" class="info-item"> 87%</van-col>
      <van-col span="24" class="info-item">
        <span class="item-red">22</span> /
        <span class="item-blue">20902</span>
      </van-col>
      <van-col span="24" class="info-item item-tip"></van-col>
      <van-col span="24" class="info-item"> 985 + 211</van-col>
      <van-col span="24" class="info-item"> </van-col>
      <van-col span="24" class="info-item"> 022-88886888</van-col>
      <van-col span="24" class="info-item"> //</van-col>
      <van-col span="24" class="info-item">
        <span class="item-blue">http://www.tju.edu.cn/</span>
      </van-col>
    </van-row>

    <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
    <van-row>
      <van-col span="24" class="chart-tips">
         
      </van-col>
    </van-row>
    <wap-history class="chart-position" :chart1="data.chart1"></wap-history>

    <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
    <van-row>
      <van-col span="24" class="chart-tips">
        
      </van-col>
    </van-row>
    <wap-chart-underscore class="chart-position" :year="new Date().getFullYear() - 3 "></wap-chart-underscore>

    <wap-subtitle subtitle="" style="margin-top: 20px;"></wap-subtitle>
    <van-row>
      <van-col span="24" class="chart-tips">
        
      </van-col>
      <van-col span="24" class="chart-tips">
         2015 2016 2017
      </van-col>
    </van-row>
    <wap-chart-course-percent class="chart-position"></wap-chart-course-percent>
    <wap-chart-course-score class="chart-position" :year="new Date().getFullYear() - 3 "></wap-chart-course-score>
    <wap-chart-course-score class="chart-position" :year="new Date().getFullYear() - 2 "></wap-chart-course-score>
    <wap-chart-course-score class="chart-position" :year="new Date().getFullYear() - 1"></wap-chart-course-score>

    <van-row :gutter="20">
      <van-col span="24" class="tip-red">
      </van-col>
    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      form: {},
      data: {
        chart1: []
      }
    }
  },
  methods: {
    fetchData() {
      const param = {
        wxId: this.$store.state.userinfo.openid,
        id: this.$route.query.id
      }
      this.$api.GetTestAnswerById(param).then(res => {
        if (res.data.code == '200') {
          debugger
        }
      })
    }
  },
  created() {}
}
</script>


<style lang="scss" scoped>
.info {
  font-size: 13px;
  padding-top: 10px;
  .info-item {
    padding-top: 5px;
    .item-red {
      color: #cc0000;
    }

    .item-blue {
      color: #0066ff;
    }

    &.item-tip {
      color: #cc0000;
      font-size: 12px;
    }
  }

  .rate {
    display: inline-block;
    position: relative;
  }
}

.chart-item {
  margin-top: 20px;
}

.chart-position {
  bottom: -20px;
}

.tip-red {
  color: #cc0000;
  font-size: 12px;
}

.chart-tips {
  color: #cc0000;
  font-size: 12px;
  padding: 10px 5px;
}
</style>



