<template>
  <ve-bar :data="chartData" :settings="chartSettings" :extend="chartExtend" height="400px"></ve-bar>
</template>

<script>
module.exports = {
  props: {
    year: {
      default: new Date().getFullYear()
    }
  },
  created() {
    this.chartData = {
      columns: ['major', 'percent'],
      rows: [
        { major: '', percent: 95 },
        { major: '', percent: 80 },
        { major: '', percent: 70 },
        { major: '', percent: 60 },
        { major: '', percent: 50 },
        { major: '', percent: 40 },
        { major: '', percent: 30 },
        { major: '', percent: 20 },
        { major: '', percent: 19 },
        { major: '', percent: 15 }
      ].reverse()
    }
    this.chartSettings = {
      metrics: ['percent'],
      label: {
        normal: { show: true, formatter: '{c}%' }
      },
      max: [100],
      legendName: {
        percent: ``
      },
      itemStyle: {
        color(item) {
          if (item.data >= 95) {
            return '#1cd4ae'
          } else if (item.data >= 80 && item.data < 95) {
            return '#cc0000'
          } else if (item.data >= 55 && item.data < 80) {
            return '#33CCFF'
          } else {
            return '#cc3333'
          }
        }
      }
    }

    this.chartExtend = {}
  }
}
</script>


